<template>
  <div class="show-chart">
    <el-card class="container">
      <div>
        <!--  -->
        <h3 class="month-title">{{ getMonth() }}</h3>
        <!--  -->
        <el-button-group>
          <el-button type="info" size="mini" @click="showAll"></el-button>
          <el-button type="danger" size="mini" @click="showExpense">
            
          </el-button>
          <el-button type="success" size="mini" @click="showIncome">
            
          </el-button>
        </el-button-group>
      </div>

      <!--echarts-->
      <div
        id="chart-div"
        style="width: 100%; height: 400px; margin: 0 auto;"
      ></div>

      <div class="btn-group">
        <el-button
          type="primary"
          @click="handleBackHome"
          style="margin-right:50px"
        >
          
        </el-button>
        <el-button type="success" @click="reSize"></el-button>
      </div>
    </el-card>
  </div>
</template>

<script>
import echarts from 'echarts'

export default {
  name: 'ShowChart',
  data () {
    return {
      // 
      chartData: [],
      // 
      showData: [],
      echartFig: Object
    }
  },
  computed: {
    // 
    legends () {
      var result = []
      for (let i = 0; i < this.showData.length; i++) {
        result.push(this.showData[i].name)
      }
      return result
    },
    perDict () {
      // 
      var sum = 0.0
      for (let i = 0; i < this.showData.length; i++) {
        sum += this.showData[i].value
      }
      // 
      var result = {}
      for (let i = 0; i < this.showData.length; i++) {
        result[this.showData[i].name] = (100 * this.showData[i].value) / sum
      }
      return result
    }
  },
  methods: {
    // 
    getMonth () {
      var chosenMonth = this.$store.state.chosenMonth
      if (chosenMonth) {
        return ': ' + chosenMonth
      } else {
        return ''
      }
    },
    // 
    handleBackHome () {
      this.$router.push('/')
    },
    // 
    showAll () {
      this.showData = this.chartData
      this.drawLine()
    },
    // 
    showExpense () {
      this.showData = this.chartData.filter(item => {
        if (item.type === 0) {
          return item
        }
      })
      this.drawLine()
    },
    // 
    showIncome () {
      this.showData = this.chartData.filter(item => {
        if (item.type === 1) {
          return item
        }
      })
      this.drawLine()
    },
    // 
    reSize () {
      this.echartFig.resize()
    },
    drawLine () {
      const _this = this
      // domecharts
      this.echartFig = echarts.init(
        document.getElementById('chart-div'),
        'light'
      )
      // 
      this.echartFig.setOption({
        tooltip: {
          trigger: 'item'
        },
        // 
        legend: {
          x: 'right',
          y: 'top',
          orient: 'vertical',
          data: _this.legends,
          formatter: function (name) {
            return name + ': ' + _this.perDict[name].toFixed(2) + '%'
          }
        },
        // 
        series: [
          {
            type: 'pie',
            center: ['50%', '55%'],
            radius: ['20px%', '100px'],
            clockwise: true,
            avoidLabelOverlap: true,
            // 
            label: {
              show: false,
              position: 'outside',
              formatter: '{b}{d}%\n'
            },
            // 
            data: this.showData
          }
        ]
      })
    }
  },
  beforeMount () {
    // (category)
    var chartData = []
    var billTable = this.$store.state.billTable

    for (let i = 0; i < billTable.length; i++) {
      var exist = false
      for (let j = 0; j < chartData.length; j++) {
        if (chartData[j].name === billTable[i].category) {
          chartData[j].value += parseFloat(billTable[i].amount)
          exist = true
          break
        }
      }
      if (!exist) {
        chartData.push({
          name: billTable[i].category,
          value: parseFloat(billTable[i].amount),
          type: billTable[i].type
        })
      }
    }
    for (let i = 0; i < chartData.length; i++) {
      chartData[i].name = this.$store.state.catDict[chartData[i].name]
    }
    chartData.sort((a, b) => {
      return b.value - a.value
    })

    // this.data
    this.chartData = chartData
    this.showData = chartData
  },
  mounted () {
    // echart
    this.drawLine()
  }
}
</script>

<style lang="scss" scoped>
.container {
  margin: 0 auto;
  padding: 30px 20px;
  text-align: center;
  width: 600px;
  min-width: 300px;
}
.month-title {
  margin: 0 0 10px 0;
}
@media screen and (max-width: 768px) {
  .container {
    width: 80%;
  }
}
</style>
