<template>
  <div class="home">
    <div class="container">
      <!--  -->
      <el-row :gutter="20">
        <el-col :xs="12" :sm="9" :md="9" :lg="9" :xl="9" class="md-10">
          <el-input
            placeholder=""
            prefix-icon="el-icon-search"
            v-model="keyword"
          ></el-input
        ></el-col>
        <el-col :xs="12" :sm="5" :md="5" :lg="5" :xl="5" class="md-10">
          <UploadBtn
            btnName=""
            btnId="category-btn"
            @readed="handleCatCSV"
          />
        </el-col>
        <el-col :xs="12" :sm="5" :md="5" :lg="5" :xl="5" class="md-10">
          <UploadBtn
            btnName=""
            btnId="bill-btn"
            @readed="handleBillCSV"
          />
        </el-col>
        <el-col :xs="12" :sm="5" :md="5" :lg="5" :xl="5" class="md-10">
          <el-button
            size="medium"
            type="danger"
            plain
            class="block h-40"
            :disabled="billTable.length ? false : true"
            @click="handleExport"
          >
            
          </el-button>
        </el-col>
      </el-row>

      <!--  -->
      <el-row :gutter="20">
        <el-col :xs="12" :sm="9" :md="9" :lg="9" :xl="9" class="md-10">
          <DropdownBtn
            :itemList="months"
            :btnName="''"
            :defaultCommand="chosenMonth"
            @clickCommand="handleCommand"
            resetBtn=""
          />
        </el-col>
        <el-col :xs="12" :sm="5" :md="5" :lg="5" :xl="5" class="h-40 md-10">
          <el-checkbox
            v-model="displayOp.type"
            label=""
            class="block"
          ></el-checkbox>
          <el-checkbox
            v-model="displayOp.cat"
            label=""
            class="block"
            :disabled="catTable.length ? false : true"
          ></el-checkbox>
        </el-col>
        <el-col :xs="12" :sm="5" :md="5" :lg="5" :xl="5" class="md-10">
          <el-button
            size="medium"
            type="success"
            class="block h-40"
            @click="handleAddBill"
            >
          </el-button>
        </el-col>
        <el-col :xs="12" :sm="5" :md="5" :lg="5" :xl="5" class="md-10">
          <el-button
            size="medium"
            type="warning"
            class="block h-40"
            plain
            @click="handleClickChart"
            :disabled="billTable.length && catTable.length ? false : true"
          >
            
          </el-button>
        </el-col>
      </el-row>

      <!--  -->
      <BillTable ref="elBillTable" :billTable="disBillTable" />

      <!--  -->
      <div style="float: right">
        <el-tag type="danger" class="mr-10"
          >{{ summary.expense }}</el-tag
        >
        <el-tag type="success">{{ summary.income }}</el-tag>
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import UploadBtn from '@/components/UploadBtn.vue'
import DropdownBtn from '@/components/DropdownBtn.vue'
import BillTable from '@/components/BillTable.vue'
import exportFromJSON from 'export-from-json'
import moment from 'moment'

import {
  parseBillCSV,
  parseCatCSV,
  getMonths,
  changeTypeDisplay,
  changeCatDisplay,
  filterByMonth,
  filterByKeyword,
  getCatDict,
  calSummary
} from '@/utils/home'

export default {
  name: 'Home',
  data () {
    return {
      // 
      keyword: '',
      // yyyy-MM
      chosenMonth: '',
      // 
      displayOp: {
        type: false,
        cat: false
      },
      // 
      billTable: [],
      // 
      catTable: []
    }
  },
  components: {
    UploadBtn,
    DropdownBtn,
    BillTable
  },
  computed: {
    // 
    disBillTable () {
      // 
      var result = JSON.parse(JSON.stringify(this.billTable))
      if (this.displayOp.type) {
        result = changeTypeDisplay(result, this.$store.state.typeDict)
      }
      if (this.displayOp.cat) {
        result = changeCatDisplay(result, this.$store.state.catDict)
      }
      if (this.chosenMonth) {
        result = filterByMonth(result, this.chosenMonth)
      }
      if (this.keyword) {
        result = filterByKeyword(result, this.keyword)
      }
      return result
    },
    // 
    months () {
      return getMonths(this.billTable)
    },
    // 
    summary () {
      return calSummary(this.billTable, this.chosenMonth)
    }
  },
  methods: {
    // 
    handleCommand (command) {
      this.chosenMonth = command.name
      this.$store.commit('setChosenMonth', command.name)
    },
    // 
    handleBillCSV (itemList) {
      this.billTable = parseBillCSV(itemList)
      this.$store.commit('setBillTable', this.billTable)
      this.$message({
        message: '',
        type: 'success',
        duration: 1500
      })
    },
    // 
    handleCatCSV (itemList) {
      this.catTable = parseCatCSV(itemList)
      this.$store.commit('setCatTable', this.catTable)
      this.$store.commit('setCatDict', getCatDict(this.catTable))
      this.$message({
        message: '',
        type: 'success',
        duration: 1500
      })
    },
    // 
    handleAddBill () {
      this.$router.push('addBill')
    },
    // 
    handleClickChart () {
      this.$router.push('showChart')
    },
    // 
    handleExport () {
      const bill = this.billTable
      // unix
      for (var idx in bill) {
        bill[idx].time = moment(bill[idx].time).format('x')
      }
      const fileName = 'export-bill'
      const exportType = 'csv'
      exportFromJSON({ bill, fileName, exportType })
    }
  },
  // state
  beforeMount () {
    this.billTable = this.$store.state.billTable
    this.catTable = this.$store.state.catTable
    this.chosenMonth = this.$store.state.chosenMonth
  }
}
</script>

