<template>
  <div class="add-cat">
    <el-card class="container">
      <el-form :model="inputCat" class="input-form">
        <el-form-item>
          <el-input v-model="inputCat.id" placeholder="ID"> </el-input>
        </el-form-item>

        <el-form-item>
          <el-input v-model="inputCat.name" placeholder="">
          </el-input>
        </el-form-item>

        <el-form-item>
          <DropdownBtn
            :itemList="typeList"
            :btnName="''"
            @clickCommand="handleCommand"
          />
        </el-form-item>
      </el-form>

      <div class="btn-group">
        <el-button
          type="primary"
          @click="handleBackAddBill"
          style="margin-right:50px"
        >
          
        </el-button>
        <el-button type="success" @click="handleAddCat"></el-button>
      </div>
    </el-card>
  </div>
</template>

<script>
import DropdownBtn from '@/components/DropdownBtn.vue'

export default {
  name: 'AddCat',
  components: {
    DropdownBtn
  },
  data () {
    return {
      inputCat: {
        id: undefined,
        name: undefined,
        type: undefined
      },
      typeList: [
        {
          name: '',
          value: 0
        },
        {
          name: '',
          value: 1
        }
      ]
    }
  },
  methods: {
    handleBackAddBill () {
      this.$router.push('addBill')
    },
    handleCommand (command) {
      this.inputCat.type = command.value
    },
    handleAddCat () {
      // 
      var flag = true
      for (var attr in this.inputCat) {
        if (this.inputCat[attr] === undefined) {
          flag = false
          this.$message({
            message: '',
            type: 'warning',
            duration: 1500
          })
          break
        }
      }
      if (flag) {
        // 
        this.$store.commit('addCat', this.inputCat)
        this.$message({
          message: '',
          type: 'success',
          duration: 1500
        })
        this.inputCat = {}
        this.$router.push('/')
      }
    }
  }
}
</script>

