<template>
  <div class="add-bill">
    <el-card class="container">
      <el-form>
        <el-form-item>
          <el-row :gutter="20">
            <el-col :span="12">
              <DropdownBtn
                :itemList="catTable"
                :btnName="''"
                @clickCommand="handleCommand"
              />
            </el-col>
            <el-col :span="12">
              <el-button type="info" plain @click="handleAddCat"
                ></el-button
              >
            </el-col>
          </el-row>
        </el-form-item>

        <el-form-item>
          <el-tag :type="tagType" style="width:100px; font-size:14px">
            {{ chosenCat.typeName }}
          </el-tag>
        </el-form-item>

        <el-form-item>
          <el-row :gutter="20">
            <el-col :span="12">
              <el-date-picker
                v-model="inputBill.time"
                type="datetime"
                value-format="timestamp"
                placeholder=""
                :picker-options="pickerOptions"
              >
              </el-date-picker>
            </el-col>
          </el-row>
        </el-form-item>

        <el-form-item>
          <el-row :gutter="20">
            <el-col :span="12">
              <el-input v-model="inputBill.amount" placeholder="">
                <template slot="append"></template>
              </el-input>
            </el-col>
          </el-row>
        </el-form-item>
      </el-form>

      <div class="btn-group">
        <el-button
          type="primary"
          @click="handleBackHome"
          style="margin-right:50px"
        >
          
        </el-button>
        <el-button type="success" @click="handleAddBill"></el-button>
      </div>
    </el-card>
  </div>
</template>

<script>
import DropdownBtn from '@/components/DropdownBtn.vue'
import moment from 'moment'

export default {
  name: 'AddBill',
  components: {
    DropdownBtn
  },
  data () {
    return {
      inputBill: {
        time: undefined,
        category: undefined,
        type: undefined,
        amount: undefined
      },
      chosenCat: {
        id: '',
        name: '',
        type: undefined,
        typeName: '---'
      },
      // 
      catTable: [],
      // el-date-picker, 
      pickerOptions: {
        disabledDate (time) {
          //  - 8.64e7 
          return time.getTime() > Date.now()
        }
      }
    }
  },
  computed: {
    tagType () {
      if (this.chosenCat.type === 0) {
        return 'danger'
      } else if (this.chosenCat.type === 1) {
        return 'success'
      }
      return 'info'
    }
  },
  methods: {
    handleBackHome () {
      this.$router.push('/')
    },
    handleAddCat () {
      this.$router.push('addCat')
    },
    handleAddBill () {
      // 
      var flag = true
      for (var attr in this.inputBill) {
        if (this.inputBill[attr] === undefined) {
          flag = false
          this.$message({
            message: '',
            type: 'warning',
            duration: 1500
          })
          break
        }
      }
      // 
      if (flag) {
        // 
        if (isNaN(this.inputBill.amount)) {
          this.$message({
            message: '',
            type: 'warning',
            duration: 1500
          })
        } else {
          // 
          this.inputBill.time = moment(this.inputBill.time).format()
          // 2
          this.inputBill.amount = parseFloat(this.inputBill.amount).toFixed(2)
          // 
          this.$store.commit('addBill', this.inputBill)
          this.$message({
            message: '',
            type: 'success',
            duration: 1500
          })
          this.inputBill = {}
          this.$router.push('/')
        }
      }
    },
    // 
    handleCommand (command) {
      this.chosenCat = command
      //   
      // 
      this.chosenCat.typeName = this.$store.state.typeDict[command.type]
      this.inputBill.category = command.id
      this.inputBill.type = command.type
    }
  },
  beforeMount () {
    this.catTable = this.$store.state.catTable
  }
}
</script>

